$darkest-blue: #000444;
$lighter-blue: #002644;

body {
    background: $darkest-blue;
    color: white;
}

.topbar {
    background-color: #002644;
    display: flex;
    justify-content: space-between;
    height: 150px;
}

.navbar {
    list-style: none;
    width: 150px;
    display: flex;
    gap: 10px;
}

.logo {
    border: solid purple;
    width: 100px;
}

.navbar li {
    border: solid red;
    align-self: center;
    /*
    Text sizing
    */
}

.landing {
    border: solid blue;
    height: calc(100svh - 300px);
    display: grid;
    grid-template-columns: 1fr 500px 1fr;
    grid-template-rows: auto;
    align-content: center;
    }

.landing > * {
    justify-self: center;
}

.landing > h1 {
    grid-column: 2;
    grid-row: 1;
    border: solid green;
}

.landing > h2 {
    grid-column: 2;
    grid-row: 2;
    border: solid green;
}

.landing > p {
    grid-column: 2;
    grid-row: 3;
}

.workhistory {
    border: solid lightblue;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
}

.workhistory > div {
    width: 97%;
    border: dotted green;
    margin: 10px;
}

.listing {
    margin-left: 150px;
    border: solid blue;
}

.skills {
    border: solid purple;
    height: 100svh;
}

.skills > h1 {
    border: solid green;
    width: 50%;
    height: 50px;
}

.skillsgroup {
    border: solid green;
    display: flex;
}

.skillgroup {
    border: solid indianred;
}